<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        interval: 10,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* 
* 
*/

/*  */
* {
  transition: all .3s;
}
/*  */
html {
  color: rgb(222,222,222); background: rgb(0,43,54);
}
/*  */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  margin: .5em;
  overflow: auto;
  width: 45vw; height: 90vh;
}
/*  */
.token.selector{ color: rgb(133,153,0); }
.token.property{ color: rgb(187,137,0); }
.token.punctuation{ color: yellow; }
.token.function{ color: rgb(42,161,152); }

/*  3D  */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg) translateZ(-100px) ;
          transform: rotateY(10deg) translateZ(-100px) ;
}

/*  */
.resumeEditor{
  position: fixed; right: 0; top: 0;
  padding: .5em;  margin: .5em;
  width: 48vw; height: 90vh;
  border: 1px solid;
  background: white; color: #222;
  overflow: auto;
}
/*  */


`,`
/* 
 *  Markdown 
 *  HTML 
 */
`,`
/*  HTML  */
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}
`],
        currentMarkdown: '',
        fullMarkdown: `### 

5C



58208+

5000+POI





9+100+


#### 
* http://www.szshuwei.com/index.html
* [Boss](https://www.zhipin.com/job_detail/0e09f186783b27b01XB-39-4GVU~.html)
* zelus.wu@nf-3.com
* ![](https://raw.githubusercontent.com/zvrr/anires/master/static/WechatIMG5.png)

#### 

#### 
1. 
2. 
3. 100T
2. 
4. ETL

#### 
1. 3//
3. Java/ScalaC++Python
4. HadoopFlumeSparkHbaseLuceneESMRContributorCommitter
3. Mysql/Postgresqlsql;
4. , 
4. 
5. 

#### JAVA

#### 

1. 
2. LinuxJava
3. 
4. 

#### 
1. 
2. 3JAVAMVCUML
3. Springboot,dubbo, 
4. shellawk
5. MysqlPostgresql
6. 
